ction*/\r\n.task_container {\r\n  width: 100%;\r\n  height: 100%;\r\n  z-index: 1;\r\n  display: none;\r\n  background-color: rgba(0, 0, 0, 0.5);\r\n  position: fixed;\r\n  top: 0;\r\n  left: 0;\r\n  backdrop-filter: blur(2px);\r\n}\r\n\r\n.modal_container {\r\n  background-color: rgba(255, 255, 255);\r\n  border-top-left-radius: 1em;\r\n  border-top-right-radius: 1em;\r\n  height: 60%;\r\n  width: 100%;\r\n  position: absolute;\r\n  top: 40%;\r\n  overflow-y: auto;\r\n  padding-bottom: 1em;\r\n}\r\n\r\n.modal_container::-webkit-scrollbar {\r\n  width: 8px;\r\n}\r\n\r\n.modal_container::-webkit-scrollbar-thumb {\r\n  background-color: rgba(0, 0, 0, 0.3);\r\n  border-radius: 4px;\r\n}\r\n\r\n.modal_container::-webkit-scrollbar-thumb:hover {\r\n  background-color: rgba(0, 0, 0, 0.5); /* Scrollbar thumb color on hover */\r\n}\r\n\r\n.line1 {\r\n  margin-right: 40%;\r\n  margin-left: 40%;\r\n  margin-bottom: 1em;\r\n  margin-top: 0.5em;\r\n  border-style: solid;\r\n  border-radius: 0.3em;\r\n  color: grey;\r\n}\r\n\r\n.line {\r\n  border-color: rgba(0, 0, 0, 0.1);\r\n  margin-right: 10%;\r\n  margin-left: 10%;\r\n  margin-top: 1em;\r\n  box-shadow: 0px 2px 10px rgba(255, 255, 255, 0.3);\r\n}\r\n\r\n.modalhead {\r\n  color: rgba(0, 0, 0, 0.6);\r\n  text-align: center;\r\n}\r\n\r\n.project_label {\r\n  display: block;\r\n  margin-top: 1em;\r\n  margin-left: 1em;\r\n  color: rgba(0, 0, 0, 0.8);\r\n  font-size: 1.5em;\r\n}\r\n\r\n.project_select {\r\n  width: 80%;\r\n  margin-left: 2em;\r\n  margin-top: 1em;\r\n  height: 4em;\r\n  border: none;\r\n  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);\r\n  border-radius: 1em;\r\n  padding: 1em;\r\n  outline: none;\r\n  background-color: rgba(0, 0, 0, 0.1);\r\n  color: rgba(0, 0, 0, 0.5);\r\n  font-size: 1em;\r\n}\r\n\r\n.button_container {\r\n  display: flex;\r\n  margin-left: 2em;\r\n  gap: 1em;\r\n  margin-top: 2em;\r\n  justify-content: center;\r\n}\r\n\r\n.cancel_btn {\r\n  border-color: #42a5f5;\r\n  background-color: white;\r\